import React from 'react';

const Thumbnail= ({ thumb, handleClick, index }) => {
    
    return (
        <div style={styles}>
            <img src={thumb.image}
                style={{ 
                    width: '100%',
                    height: '100%'
                }}
                onClick={handleClick}
                data-index={index}
            />
        </div>
    )
}


const styles = {
    height: '50%',
    width: '25%',

}
export default Thumbnail;